const {platform, userAgent} = navigator;

export const IS_IE = userAgent.includes('MSIE');
export const IS_OPERA = userAgent.toLowerCase().includes('opera');
export const IS_FIREFOX = userAgent.toLowerCase().includes('firefox');
export const IS_SAFARI = userAgent.toLowerCase().includes('safari') && !userAgent.toLowerCase().includes('chrome');
export const IS_CHROME = !IS_IE && !IS_OPERA && !IS_FIREFOX && !IS_SAFARI; // FIXME
export const IS_OSX = platform.includes('Mac');
export const IS_WIN32 = platform.includes('Win');

export const DEFAULT_DPI = 96;
export const HEADER_SIZE = 8;
export const PIXEL_STEP  = 10;
export const LINE_HEIGHT = 40;
export const PAGE_HEIGHT = 800;
export const PING_FREQUENCY = 5000;
export const MIN_START_BUFFERS = 3;
export const MAX_BUFFERS = 250;
export const CHUNK_SZ = 0x8000;
export const DOM_KEY_LOCATION_RIGHT = 2;

export const CHARCODE_TO_NAME = (() => {
  const result = {
    8: 'BackSpace',
    9: 'Tab',
    12: 'KP_Begin',
    13: 'Return',
    16: 'Shift_L',
    17: 'Control_L',
    18: 'Alt_L',
    19: 'Pause',   // pause/break
    20: 'Caps_Lock',
    27: 'Escape',
    31: 'Mode_switch',
    32: 'space',
    33: 'Prior',   // Page Up
    34: 'Next',   // Page Down
    35: 'End',
    36: 'Home',
    37: 'Left',
    38: 'Up',
    39: 'Right',
    40: 'Down',
    42: 'Print',
    45: 'Insert',
    46: 'Delete',
    58: 'colon',
    59: 'semicolon',
    60: 'less',
    61: 'equal',
    62: 'greater',
    63: 'question',
    64: 'at',
    91: 'Menu',   // Left Window Key
    92: 'Menu',   // Right Window Key
    93: 'KP_Enter',  // 'select key'?
    106: 'KP_Multiply',
    107: 'KP_Add',
    109: 'KP_Subtract',
    110: 'KP_Delete',
    111: 'KP_Divide',
    144: 'Num_Lock',
    145: 'Scroll_Lock',
    160: 'dead_circumflex',
    167: 'underscore',
    161: 'exclam',
    162: 'quotedbl',
    163: 'numbersign',
    164: 'dollar',
    165: 'percent',
    166: 'ampersand',
    168: 'parenleft',
    169: 'parenright',
    170: 'asterisk',
    171: 'plus',
    172: 'bar',
    173: 'minus',
    174: 'braceleft',
    175: 'braceright',
    176: 'asciitilde',
    186: 'semicolon',
    187: 'equal',
    188: 'comma',
    189: 'minus',
    190: 'period',
    191: 'slash',
    192: 'grave',
    219: 'bracketleft',
    220: 'backslash',
    221: 'bracketright',
    222: 'apostrophe',
  };

  let i;

  for (i = 0; i < 26; i++) {
    result[65 + i] = 'abcdefghijklmnopqrstuvwxyz'[i];
  }

  for (i = 0; i < 10; i++) {
    result[48 + i] = '' + i;
    result[96 + i] = '' + i;
    // fix for OSX numpad?
    // CHARCODE_TO_NAME[96+i] = 'KP_'+i;
  }

  for (i = 1; i <= 24; i++) {
    result[111 + i] = 'F' + i;
  }

  return result;
})();

export const KEY_TO_NAME = (() => {
  const result = {
    "Escape"      : "Escape",
    "Tab"       : "Tab",
    "CapsLock"      : "Caps_Lock",
    "ShiftLeft"     : "Shift_L",
    "ControlLeft"   : "Control_L",
    "MetaLeft"      : "Meta_L",
    "AltLeft"     : "Alt_L",
    "Space"       : "space",
    "AltRight"      : "Alt_R",
    "MetaRight"     : "Meta_R",
    "ContextMenu"   : "Menu_R",
    "ControlRight"    : "Control_L",
    "ShiftRight"    : "Shift_R",
    "Enter"       : "Return",
    "Backspace"     : "BackSpace",
    //special keys:
    "ScrollLock"    : "Scroll_Lock",
    "Pause"       : "Pause",
    "NumLock"     : "Num_Lock",
    //Arrow pad:
    "Insert"      : "Insert",
    "Home"        : "Home",
    "PageUp"      : "Prior",
    "Delete"      : "Delete",
    "End"       : "End",
    "PageDown"      : "Next",
  };

  for (let i = 0; i <= 9; i++) {
    result['Numpad' + i] = '' + i;
    result['KP' + i] = 'KP' + i;
  }
  for (let i = 1; i <= 20; i++) {
    result['F' + i]  = 'F' + i;
  }

  return result;
})();

